body{
    /* background-color: #DEACF5; */
    background-image: url(https://i.pinimg.com/564x/91/8a/e5/918ae5203e6306bfebd752148c922d4d.jpg);
    background-repeat: no-repeat;
    background-size: cover;
}
.App{
    width: 1000px;
    margin: 0 auto;
    margin-top: 140px;
    display: flex;
    padding: 40px 40px;
    flex-direction: column;
    align-items: center;
    background: rgba(225, 225, 225, 0.2);
    backdrop-filter: blur(8px);
    
}
h1{
   font-family:'Courier New', Courier, monospace;
   color: rgb(175, 85, 20); 
   margin-left: 90px;
}
.btn{
   color: red;
   width: 40px;
   height: 30px;
}
.btn:hover{
    background-color: rgb(70, 226, 70);
}
.btn2{
    color: green;
    width: 40px;
   height: 30px;
}
.btn2:hover{
    background-color: rgb(252, 66, 66);
}
.input{
    width: 300px;
    height: 20px;
    color: chocolate;
    font-family: monospace;

}
.add{
 border-radius: 5px;
    height: 27px;
}
.add:hover{
   box-shadow: 0px 0px 6px 6px chocolate;
   transform: scale(1.1);

}
.text{
    color: brown;
    font-family: 'Courier New', Courier, monospace;
    font-size: 20px;
    font-style: italic;
}
.container{
    margin-top: -350px;
    position: absolute;
    margin-left: 120px;
    transform: translate(-50%,50%);
}
.container h3{
    font-size: 8vw;
    position: absolute;
    transform: translate(-50%,50%);
}
.container h3:nth-child(1){
    color: #8ad6e4;
    -webkit-text-stroke: 2px #3b0aee;
}
.container h3:nth-child(2){
    color: rgb(62, 109, 209);
    animation: animate 4s ease-in-out infinite;
}
@keyframes animate{
    0%,
    100%{
        clip-path: polygon(0% 45%,16% 44%, 33% 50%, 54% 60%, 70% 61%,84% 59%,100% 52%, 100% 100%, 0% 100%);
    }
    50%{
        clip-path: polygon(0% 60%,15% 65%, 34% 66%, 51% 62%, 67% 50%,84% 45%,100% 46%, 100% 100%, 0% 100%); 
    }
}


@media(max-width:600px){
    body{
        background-size: cover;
        background-repeat: initial;
        background-image: url(https://i.pinimg.com/564x/a0/26/b5/a026b5306601928cd3d854b1849fb3d4.jpg);
    }
   h3{
    margin-left: -1px;
    margin-top: 250px;
   }
   .App{
    margin-left: -230px;
    margin-top: 260px;
   }
}
