body{
    background-image: url(https://i.pinimg.com/564x/a0/26/b5/a026b5306601928cd3d854b1849fb3d4.jpg);
}
.log{
    color: #685e5e;
    outline: none;
    border: none;
    text-decoration: none;
    text-transform: uppercase;
    font-size: 40px;
    letter-spacing:  0.1em;
    font-weight: 400;
    padding: 10px 30px;
    transition: 0.5s;
    border-radius: 50px;
    position: center;
    margin-top: 20%;
    margin-left: 43%;
   
}
.log:hover{
    color:#04d9ff;
    letter-spacing: 0.25em;
    box-shadow:  0 0 35px #04d9ff;
}
@media(max-width:600px){
    .log{
        align-items: center;
        justify-content: center;
        margin-left: 79px;
        margin-top: 350px;
    }
}

