*{
    font-family: 'Poppins', sans-serif;
}
body{
    background-image: url(https://i.pinimg.com/564x/a0/26/b5/a026b5306601928cd3d854b1849fb3d4.jpg);
}
.login{
   display: flex;
   justify-content: center;
   align-items: center;
   flex-direction: column;
   min-height: 90vh;
}

.inputs{
    justify-content: center;
    height: 45px;
    width: 25%;
    border: none;
outline: none;
border-radius: 30px;
color: #fff;
padding: 0 0 0 45px;
background: rgba(255,255,255,0.1);
margin: 10px;
}
.submit{
    border: none;
    border-radius: 30px;
    font-size: 15px;
    height: 35px;
    outline: none;
    width: 150px;
    background: rgba(255,255,255,0.5);
    cursor: pointer;
    transition: .3s;
}
.submit:hover{
box-shadow: 1px 5px 7px 1px rgba(0,0,0,0.11);
}
.container > span{

    color: #fff;
    font-size: 50px;
    animation: glow 2s ease-in-out infinite;
}
@keyframes glow{
    0%, 100%{
        color: #fff;
        text-shadow:  0 0 12px lime, 0 0 50px lime, 0 0 100px lime;
    }
    10% ,90%{
        color: #111;
        text-shadow: none;
    }
}
.container > span:nth-child(2){
animation-delay:0.25s ;
}
.container > span:nth-child(3){
    animation-delay:0.5s ;
    }
    .container > span:nth-child(4){
        animation-delay: 0.75s;
        }
        .container > span:nth-child(5){
            animation-delay:1s ;
            }
@media(max-width:600px){
    .inputs{
        width:250px;
    }
}